@media only screen and (max-width: 768px) {
    .menu {
        margin: -180px 0 0 -51px;
        padding-bottom: 70px;
        list-style-type: none;
        transform-origin: 0% 0%;
        transform: translate(0%, 0);
        transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);
    }

    .menuToggle {
        display: block;
        position: relative;
        top: 50px;
        left: 50px;
        z-index: 1;
        -webkit-user-select: none;
        user-select: none;
    }
}

ul {
    list-style-type: none;
    padding-inline-start: 0px;
}

li {
    line-height: 2.5;
}


.mainContainer {
    margin: 15px;
}

.menuContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.container {
    justify-content: space-between;
    display: grid;
    align-self: center;
    align-content: center;
    margin: 12px 15px auto;
    max-width: 46%;
}

.columnMenu {
    display: flex;
    flex-direction: column;
    flex: 0 0 25em;
}

.name {
    font-weight: 700;
    font-size: x-large;
}

a, a:link, a:visited, a:focus, a:hover, a:active {
    color: #424242;
    text-decoration: none;
    cursor: pointer;
}

.iconItems {
    display: flex;
    justify-content: flex-start;
}

.iconItem {
    margin-right: 3%;
}

.appLeftMenu {
    margin-top: 15%;
}

header {
    margin-left: 10%;
    margin-top: 25%;
    margin-bottom: 5%;
}

footer {
    text-align: center;
}

.appLink {
    color: #61dafb;
}
